@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root{
  --white:#f5f5f5;
  --bgGreen:#239a3b;
}

body{
  font-size: 1em;
  font-weight: 400;
  
  line-height: 1.5;
 
 
  color: #4a4a4a;
 
  position: relative;
  font-family: 'Montserrat', sans-serif;


}

.circle.small {
	border: 20px solid var(--bgGreen);
	border-radius: 50%;
	width: 200px;
	height: 200px;
	
    margin: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    font-size: 14px;
    font-weight: bolder;
    text-align: center;
}

.circle.filled {
	background-color: #666666;
}

::-webkit-scrollbar {
    width: 12px;
}
 
::-webkit-scrollbar-track {
   
    border-radius: 10px;
}
 
::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: var(--bgGreen);
}




